import React from "react"
import { Skeleton, Typography } from "@rp/react/web/mui/core"

type BriefReportTitleProps = {
  title: string | undefined
}

const BriefReportHeader = ({ title }: BriefReportTitleProps) => {
  if (!title) return <Skeleton height={72} width={600} sx={{ marginTop: "-12px" }} />

  return (
    <Typography
      sx={{
        fontFamily: "ModernoFBCond",
        fontSize: "48px",
        variant: "h1",
        lineHeight: "100%",
        marginBottom: "28px",
      }}
      role="article"
      aria-label="brief report header"
    >
      {title}
    </Typography>
  )
}

export { BriefReportHeader }
